import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import CreateItem from 'pages/CreateItem';
import ItemGrid from 'pages/ItemGrid';
import ItemLoading from 'pages/ItemLoading';
import SkeletonGrid from 'pages/SkeletonGrid';
import collageapi from 'api/collage';
import BasePage from 'pages/base_page';

function convertToItem(thumbs) {
  return thumbs.map((thumb) => ({
    ...thumb,
    photo: { title: thumb.collage_id, src: thumb.thumbnail_url },
  }));
}

async function getItems(
  handleItems,
  handleToken,
  handleItemDetails,
  nextToken = ''
) {
  try {
    const listResp = await collageapi.listCollages(nextToken);
    handleItems(listResp.collage_ids);
    handleToken(listResp.next_token);
    const collagePromises = listResp.collage_ids.map((collage) =>
      collageapi.getThumbnail(collage)
    );
    const collThumbs = await Promise.all(collagePromises);
    handleItemDetails(convertToItem(collThumbs));
  } catch (error) {
    /* eslint-disable no-console */
    console.log(error);
  }
}

export default function ListCollages() {
  const navigate = useNavigate();
  const [count, setCount] = useState(0);
  const [nextToken, setNextToken] = useState('');
  const [thumbnails, setThumbnails] = useState([]);
  const [collages, setCollages] = useState([]);

  const addCollages = (colls) => setCollages((cls) => cls.concat(colls));
  const addThumbnails = (thumbs) => setThumbnails((ths) => ths.concat(thumbs));

  const loadCollages = () =>
    getItems(addCollages, setNextToken, addThumbnails, nextToken);
  const handleCreateClick = () => navigate('/collage/create');
  const createComp = (
    <CreateItem message="Create a collage" handleClick={handleCreateClick} />
  );

  const sortedThumbnails = thumbnails.sort((a, b) =>
    parseInt(a.collage_id, 10) > parseInt(b.collage_id, 10) ? 1 : -1
  );

  const handleClick = (det) => {
    navigate(`/collage/${det.collage_id}`);
  };
  const handleImageLoad = () => setCount((cnt) => cnt + 1);

  useEffect(loadCollages, []);

  const itemsGrid = (
    <ItemGrid
      createComponent={createComp}
      handleClick={handleClick}
      handleImageLoad={handleImageLoad}
      handleMoreClick={loadCollages}
      items={sortedThumbnails}
      nextToken={nextToken}
    />
  );

  const skeletonGrid = (
    <>
      <SkeletonGrid>
        {createComp}
        {thumbnails.map((memory) => (
          <ItemLoading memory={memory} />
        ))}
      </SkeletonGrid>

      <Box component="div" sx={{ display: 'none' }}>
        {itemsGrid}
      </Box>
    </>
  );

  return (
    <BasePage>
      {thumbnails.length === collages.length && count >= thumbnails.length
        ? itemsGrid
        : skeletonGrid}
    </BasePage>
  );
}
